.app {
  color: #fff;
  display: flex;
  flex-direction: column;
  height: 100%;
  position: absolute;
  width: 100%;
}

.home {
  width: 100%;
  -webkit-box-flex: 1;
  -ms-flex: 1 1 auto;
  flex: 1 1 auto;
  display: flex;
  flex-direction: row;
  align-items: center;
  align-content: center;
}

.button {
  margin-left: 15px;
  border-width: 5px;
  border-style: solid;
  border-image: linear-gradient(blue, white);
}